import React from "react";
import IconButton from "@material-ui/core/IconButton";
import Tooltip from "@material-ui/core/Tooltip";
import CompareArrowsIcon from "@material-ui/icons/CompareArrows";
import EditIcon from '@material-ui/icons/Edit';
import DeleteIcon from '@material-ui/icons/Delete';
import IndeterminateCheckBoxIcon from "@material-ui/icons/IndeterminateCheckBox";
import { withStyles } from "@material-ui/core/styles";
import states from "../api/states";
import { editClient, deleteClient } from "../api/api";
import {
  Button,
  ButtonGroup,
  FormGroup,
  Form,
  Label,
  Input,
  Card,
  CardBody,
  CardHeader,
  Col,
  Modal,
  ModalBody,
  ModalFooter,
  ModalHeader,
  Row,
  Alert,
} from 'reactstrap';

const defaultToolbarSelectStyles = {
  iconButton: {
  },
  iconContainer: {
    marginRight: "24px",
  },
  inverseIcon: {
    transform: "rotate(90deg)",
  },
};

class CustomToolbarSelectClients extends React.Component {
  constructor(props)
  {
    super(props);
    this.state = {
      showEditModal:false,
      saved:false,
      id: '',
      company : '',
      contact_name: '',
      address : '',
      city:'',
      state:'nn',
      zip:'',
      email:'',
      phone:'',
      xtoken:'',
      msg: '',
      error: '',
    };
  }
  hideEditModal = () => {
    this.setState({
      ...this.state,
      showEditModal:false,
    });
    if(this.state.saved)
    {
      this.props.rerenderParentCallback();
    }
  }
  handleEditCompanyInput = (event) => {
    const v = event.target.value;
    this.setState({...this.state, company: v,});
    // console.log(v);
  }
  handleEditContactInput = (event) => {
    const v = event.target.value;
    this.setState({...this.state, contact_name: v,});
}
  handleEditAddressInput = (event) => {
      const v = event.target.value;
      this.setState({...this.state, address: v,});
  }
  handleEditCityInput = (event) => {
      const v = event.target.value;
      this.setState({...this.state, city: v,});
  }
  handleEditStateInput = (event) => {
      const v = event.target.value;
      this.setState({...this.state, state: v,});
  }
  handleEditZipInput = (event) => {
      const v = event.target.value;
      this.setState({...this.state, zip: v,});
  }
  handleEditEmailInput = (event) => {
      const v = event.target.value;
      this.setState({...this.state, email: v,});
  }
  handleEditPhoneInput = (event) => {
      const v = event.target.value;
      this.setState({...this.state, phone: v,});
  }
  handleEditFaxInput = (event) => {
      const v = event.target.value;
      this.setState({...this.state, fax: v,});
  }
  handleEditSaveChanges = () => {
      // console.log('id is', this.state);
      editClient(this.state.xtoken, this.state, this.state.id)
      .then((response) => {
          if( response.status === 200)
          {
            this.setState({
              ...this.state,
              saved:true,
              error: '',
              msg: response.message,
            });
          }
          else
          {
              this.setState({
                  ...this.state,
                  error: response.message,
                  msg: '',
              });
          }
      })
      .catch((error) => {
          this.setState({
              ...this.state,
              error: 'Connection error occured',
          });
          console.log('error', error);
      });
  }
  handleClickEditRow = () => {
    let dataIndex = this.props.selectedRows.data[0]['dataIndex'];
    let selectedRowData = this.props.originalData[dataIndex];
    this.setState({
      showEditModal:true,
      id : selectedRowData.id,
      company : selectedRowData.company,
      contact_name : selectedRowData.contact_name,
      address : selectedRowData.address,
      city:selectedRowData.city,
      state:selectedRowData.state,
      zip:selectedRowData.zip,
      email:selectedRowData.email,
      phone:selectedRowData.phone,
      xtoken:this.props.Xtoken,
    });
  }
  handleDeleteSelection = () => {
    let dataIndex = this.props.selectedRows.data[0]['dataIndex'];
    let selectedRowData = this.props.originalData[dataIndex];
    deleteClient(this.props.Xtoken, selectedRowData.id)
    .then((response) => {
      if(response.status === 200 )
      {
        alert(response.message);
        this.props.rerenderParentCallback();
        return;
      }
      alert(response.message);
      return;
    })
    .catch((error) => {
      alert('Connection error occured');
    });
  };

  handleClickDeselectAll = () => {
    this.props.setSelectedRows([]);
  };

  handleClickBlockSelected = () => {
    console.log(`block users with dataIndexes: ${this.props.selectedRows.data.map(row => row.dataIndex)}`);
  };

  render() {
    const { classes } = this.props;

    return (
      <div className={classes.iconContainer}>

        <Tooltip title={"Edit row"}>
          <IconButton className={classes.iconButton} onClick={this.handleClickEditRow}>
            <EditIcon className={classes.icon} />
          </IconButton>
        </Tooltip>
        <Tooltip title={"Deselect row"}>
          <IconButton className={classes.iconButton} onClick={this.handleClickDeselectAll}>
            <IndeterminateCheckBoxIcon className={classes.icon} />
          </IconButton>
        </Tooltip>
        <Tooltip title={"Delete row"}>
          <IconButton className={classes.iconButton} onClick={this.handleDeleteSelection}>
            <DeleteIcon className={classes.icon} />
          </IconButton>
        </Tooltip>
        {/* modals */}
        <Modal
          size="lg"
          isOpen={this.state.showEditModal}
          backdrop={false}>
          <ModalHeader>
            Edit client information
            {this.state.error.length > 0 && (
              <div>
                <br></br><Alert color="danger">{this.state.error}</Alert>
              </div>
            )}
            {this.state.msg.length > 0 && (
              <div>
                <br></br><Alert color="success">{this.state.msg}</Alert>
              </div>
            )}
          </ModalHeader>
          <ModalBody>
            <Row>
              <Col xl={4} lg={12} md={12}>
                <Card>
                  <CardBody>
                    <Form>
                      <FormGroup>
                        <Label for="exampleEmail">Company name*</Label>
                        <Input onChange={this.handleEditCompanyInput}
                          type="text" 
                          value={this.state.company}
                          name="company"
                          placeholder="e.g Greyson LLC"
                        />
                      </FormGroup>
                      <FormGroup>
                        <Label for="exampleEmail">Contact name*</Label>
                        <Input onChange={this.handleEditContactInput}
                          type="text" 
                          value={this.state.contact_name}
                          name="cnt"
                          placeholder=""
                        />
                      </FormGroup>
                    </Form>
                  </CardBody>
                </Card>
              </Col>

              <Col xl={4} lg={12} md={12}>
                <Card>
                  <CardBody>
                    <Form>
                     <FormGroup>
                        <Label for="examplePassword">Address*</Label>
                        <Input onChange={this.handleEditAddressInput}
                          type="text"
                          value={this.state.address}
                          name="address"
                          placeholder="e.g. 3015 Lanely park"
                        />
                      </FormGroup>
                      <FormGroup>
                        <Label for="exampleUrl">City*</Label>
                        <Input onChange={this.handleEditCityInput}
                          type="text" 
                          value={this.state.city}
                          name="city"
                          id="city"
                          placeholder="e.g. Plano"
                        />
                      </FormGroup>
                    </Form>
                  </CardBody>
                </Card>
              </Col>
      
              <Col xl={4} lg={12} md={12}>
                <Card>
                  <CardBody>
                    <Form>
                        <FormGroup>
                        <Label for="exampleSelect">State*</Label>
                        <Input onChange={this.handleEditStateInput} value={this.state.state} defaultValue={this.state.state} type="select" name="state">
                          <option value="nn" disabled={true}>Select state</option>
                          {
                              states.data.map((stt, idx) => (
                                  <option key={idx} value={stt.abbreviation}>{stt.name}</option>
                              ))
                          }
                        </Input>
                      </FormGroup>
                      <FormGroup>
                        <Label for="exampleNumber">Zip Code*</Label>
                        <Input onChange={this.handleEditZipInput}
                          type="number"
                          value={this.state.zip}
                          name="zip"
                          id="zip"
                          placeholder="zip code"
                        />
                      </FormGroup>
                    </Form>
                  </CardBody>
                </Card>
              </Col>

              <Col xl={4} lg={12} md={12}>
                <br></br>
                <Card>
                  <CardBody>
                    <Form>
                    <FormGroup>
                        <Label for="examplePassword">Company email*</Label>
                        <Input onChange={this.handleEditEmailInput}
                          type="email"
                          value={this.state.email}
                          name="email"
                          placeholder="a.valid@email.com"
                        />
                      </FormGroup>
                      <FormGroup>
                        <Label for="examplePassword">Company phone*</Label>
                        <Input onChange={this.handleEditPhoneInput}
                          type="text"
                          value={this.state.phone}
                          name="phone"
                          placeholder=""
                        />
                      </FormGroup>
                    </Form>
                  </CardBody>
                </Card>
              </Col>
            </Row>
          </ModalBody>
          <ModalFooter>
            <Button color="primary" onClick={this.handleEditSaveChanges}>
              Save changes
            </Button>{' '}
            <Button color="secondary" onClick={this.hideEditModal}>
              Close
            </Button>
          </ModalFooter>
        </Modal>
      </div>
    );
  }
}

export default withStyles(defaultToolbarSelectStyles, { name: "CustomToolbarSelectClients" })(CustomToolbarSelectClients);