import Page from 'components/Page';
import React from 'react';
import {
  Button,
  Card,
  CardBody,
  CardHeader,
  Col,
  Form,
  FormFeedback,
  FormGroup,
  FormText,
  Input,
  Label,
  Row,
  Alert,
  Table,
} from 'reactstrap';
import states from "../api/states";
import { newClient, findClients, findBillingStatus} from "../api/api";
import AsyncStorage from '@callstack/async-storage';
import conf from "../api/config";
import MUIDataTable from "mui-datatables";
import PageSpinner from 'components/PageSpinner';
import CustomToolbarSelectClients from "../components/CustomToolbarSelectClients";
class ClientsPage extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
          company : '',
          contact_name: '',
          address : '',
          city:'',
          state:'nn',
          zip:'',
          email:'',
          phone:'',
          xtoken:'',
          msg: '',
          error: '',
          downloadFile:true,
          preload:false,
          data: [],
        };
        AsyncStorage.getItem(conf.secret)
        .then((results) => {
          if( results !== null )
          {
            let obje_results = JSON.parse(results);
            this.setState({
              ...this.state,
              xtoken : obje_results.user.token,
            });
            findBillingStatus(obje_results.user.token).then((response) => {
              if( parseInt(response.expired) === 1 )
              {
                window.location.href = '/bills/payment';
              }
            }).catch((er) => console.log(er));
            findClients(obje_results.user.token).then((response) => {
                console.log('owners', response.data);
                this.setState({
                    ...this.state,
                    data:response.data,
                });
            }).catch((respError) => {

            });
          }
        })
        .catch((error) => {
          console.log('storage error', error);
        });
    }
    componentDidMount() {
    }

    isValidEmailAddress = (email) => {
        if (/^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,4}$/i.test(email)) {
            return true;
        }
        return false;
    }

    rerenderParentCallback = () => {
      findClients(this.state.xtoken).then((response) => {
        this.setState({
            ...this.state,
            data:response.data,
        });
      }).catch((respError) => {
      });
    }
    
    handleCompanyInput = (event) => {
        const v = event.target.value;
        this.setState({...this.state, company: v,});
        // console.log(v);
    }
    handleContactInput = (event) => {
        const v = event.target.value;
        this.setState({...this.state, contact_name: v,});
        // console.log(v);
    }
    handleAddressInput = (event) => {
        const v = event.target.value;
        this.setState({...this.state, address: v,});
    }
    handleCityInput = (event) => {
        const v = event.target.value;
        this.setState({...this.state, city: v,});
    }
    handleStateInput = (event) => {
        const v = event.target.value;
        this.setState({...this.state, state: v,});
    }
    handleZipInput = (event) => {
        const v = event.target.value;
        this.setState({...this.state, zip: v,});
    }
    handleEmailInput = (event) => {
        const v = event.target.value;
        this.setState({...this.state, email: v,});
    }
    handlePhoneInput = (event) => {
        const v = event.target.value;
        this.setState({...this.state, phone: v,});
    }
    handleFaxInput = (event) => {
        const v = event.target.value;
        this.setState({...this.state, fax: v,});
    }
    handleSaveChanges = () => {
        // console.log('so far', this.state);
        this.setState({...this.state, preload:true,});
        newClient(this.state.xtoken, this.state)
        .then((response) => {
            if( response.status === 200)
            {
                this.setState({
                    ...this.state,
                    company : '',
                    address : '',
                    city:'',
                    state:'nn',
                    zip:'',
                    email:'',
                    phone:'',
                    msg: response.message,
                    data: response.data,
                    error: '',
                    preload:false,
                });

                this.forceUpdate();
            }
            else
            {
                this.setState({
                    ...this.state,
                    error: response.message,
                    msg: '',
                    preload:false,
                });
            }
        })
        .catch((error) => {
            this.setState({
                ...this.state,
                error: 'Connection error occured',
                preload:false,
            });
            console.log('error', error);
        });
    }
    render() {
        const columns = [
            {
              name: 'Id',
              options: {
                display: false
              }
            },
            'Company',
            'Contact',
            'Address',
            {
              name: 'City',
              options: {
                display: true
              }
            },
            {
              name: 'State',
              options: {
                display:false,
              }
            },
            {
              name: 'Zip Code',
              options: {
                download: true,
              },
            },
            {
              name: 'Company email',
              options: {
                download: true,
              },
            },
            {
              name: 'Company phone',
              options: {
                download: true,
              },
            },
            {
              name: 'Date',
              options: {
                display: false,
              },
            },
        ];

        const data2 = () => {
          let rtn = [];
          this.state.data.map(item => {
            rtn.push([
              item.id,
              item.company,
              item.contact_name,
              item.address,
              item.city,
              item.state,
              item.zip,
              item.email,
              item.phone,
              item.created_at,
            ]);
          });
          return rtn;
        }
      
        const options = {
            filter: true,
            selectableRows: 'single',
            selectableRowsOnClick:true,
            filterType: 'dropdown',
            responsive: 'vertical',
            draggableColumns: {
              enabled: true
            },
            rowsPerPage: 5,
            selectToolbarPlacement: 'replace',
            customToolbarSelect: (selectedRows, displayData, setSelectedRows) => (
              <CustomToolbarSelectClients rerenderParentCallback={this.rerenderParentCallback} selectedRows={selectedRows} displayData={displayData} setSelectedRows={setSelectedRows}  originalData={this.state.data} Xtoken={this.state.xtoken}/>
            ),
            downloadOptions: {
                filename: 'truck-owners-excel-format.csv',
                separator: ';',
                filterOptions: {
                  useDisplayedColumnsOnly: true,
                  useDisplayedRowsOnly: true,
                }
            },
            onDownload: (buildHead, buildBody, columns, data2) => {
              if (this.state.downloadFile) {
                let val= `${buildHead(columns)}${buildBody(data2)}`.trim();
                return val;
              }
      
              return false;
            },
            onRowSelectionChange: (currentRowsSelected, allRows, rowsSelected) => {
              // console.log('currentRowsSelected', currentRowsSelected);
            },
            onRowsDelete: rowsDeleted => {
              // console.log(rowsDeleted, 'were deleted!');
            },
            onChangePage: numberRows => {
              // console.log(numberRows);
            },
            onSearchChange: searchText => {
              // console.log(searchText);
            },
            onColumnSortChange: (column, direction) => {
              // console.log(column, direction);
            },
            onViewColumnsChange: (column, action) => {
              // console.log(column, action);
            },
            onFilterChange: (column, filters) => {
              // console.log(column, filters);
            },
            onCellClick: (cellIndex, rowIndex) => {
              // console.log(cellIndex, rowIndex);
            },
            onRowClick: (rowData, rowState) => {
              // console.log(rowData, rowState);
            },
        };
      return (
        <Page title="Clients" breadcrumbs={[{ name: 'Clients', active: true }]}>
          <Row>
            <Col xl={12} lg={12} md={12}>
              <Card>
                <CardHeader>Create Client</CardHeader>
                {this.state.preload && (
                  <PageSpinner/>
                )}
                {this.state.error.length > 0 && (
                    <Alert color="danger">{this.state.error}</Alert>
                )}
                {this.state.msg.length > 0 && (
                    <Alert color="success">{this.state.msg}</Alert>
                )}
              </Card>
            </Col>
          </Row>

          <Row>
            <Col xl={3} lg={12} md={12}>
              <Card>
                <CardBody>
                  <Form>
                    <FormGroup>
                      <Label for="exampleEmail">Company name*</Label>
                      <Input onChange={this.handleCompanyInput}
                        type="text" 
                        value={this.state.company}
                        name="company"
                        placeholder="e.g Greyson LLC"
                      />
                    </FormGroup>
                    <FormGroup>
                      <Label for="exampleEmail">Contact name*</Label>
                      <Input onChange={this.handleContactInput}
                        type="text" 
                        value={this.state.contact_name}
                        name="cont"
                        placeholder=""
                      />
                    </FormGroup>
                    <FormGroup>
                      <Label for="examplePassword">Address*</Label>
                      <Input onChange={this.handleAddressInput}
                        type="text"
                        value={this.state.address}
                        name="address"
                        placeholder="e.g. 3015 Lanely park"
                      />
                    </FormGroup>
                  </Form>
                </CardBody>
              </Card>
            </Col>

            <Col xl={3} lg={12} md={12}>
              <Card>
                <CardBody>
                  <Form>
                    <FormGroup>
                      <Label for="exampleUrl">City*</Label>
                      <Input onChange={this.handleCityInput}
                        type="text" 
                        value={this.state.city}
                        name="city"
                        id="city"
                        placeholder="e.g. Plano"
                      />
                    </FormGroup>
                    <FormGroup>
                      <Label for="exampleSelect">State*</Label>
                      <Input onChange={this.handleStateInput} value={this.state.state} defaultValue={this.state.state} type="select" name="state">
                        <option value="nn" disabled={true}>Select state</option>
                        {
                            states.data.map((stt, idx) => (
                                <option key={idx} value={stt.abbreviation}>{stt.name}</option>
                            ))
                        }
                      </Input>
                    </FormGroup>
                  </Form>
                </CardBody>
              </Card>
            </Col>
    
            <Col xl={3} lg={12} md={12}>
              <Card>
                <CardBody>
                  <Form>
                    <FormGroup>
                      <Label for="exampleNumber">Zip Code*</Label>
                      <Input onChange={this.handleZipInput}
                        type="number"
                        value={this.state.zip}
                        name="zip"
                        id="zip"
                        placeholder="zip code"
                      />
                    </FormGroup>
                    <FormGroup>
                      <Label for="examplePassword">Company email*</Label>
                      <Input onChange={this.handleEmailInput}
                        type="email"
                        value={this.state.email}
                        name="email"
                        placeholder="a.valid@email.com"
                      />
                    </FormGroup>
                  </Form>
                </CardBody>
              </Card>
            </Col>

            <Col xl={3} lg={12} md={12}>
              <Card>
                <CardBody>
                  <Form>
                    <FormGroup>
                      <Label for="examplePassword">Company phone*</Label>
                      <Input onChange={this.handlePhoneInput}
                        type="text"
                        value={this.state.phone}
                        name="phone"
                        placeholder=""
                      />
                    </FormGroup>
                    <br></br>
                    <FormGroup check row>
                      <Col sm={{ size: 10, offset: 2 }}>
                        <Button type="button" onClick={this.handleSaveChanges}>Create client</Button>
                      </Col>
                    </FormGroup>
                  </Form>
                </CardBody>
              </Card>
            </Col>
          </Row>

          <Row>
            <Col xl={12} lg={12} md={12}>
              <Card>
                <CardBody>
                    <MUIDataTable title={'Clients'}
                    data={this.state.data.map(item => {
                      return [
                          item.id,
                          item.company,
                          item.contact_name,
                          item.address,
                          item.city,
                          item.state,
                          item.zip,
                          item.email,
                          item.phone,
                          item.created_at,
                      ]
                    })} columns={columns} options={options} />
                </CardBody>
              </Card>
            </Col>
          </Row>
    
        </Page>
      );
    }
}
export default ClientsPage;
